<template>
  <div class="battery">
    <!-- width:45 -->
    <svg width="25" height="10" viewBox="0 0 25 10" preserveAspectRatio="xMinYMin meet" xmlns="http://www.w3.org/2000/svg">
      <rect x="0" y="2" width="2" height="6" style="fill: #313139; stroke: #313139;" />
      <rect x="2" y="0" width="22" height="10" style="fill: transparent; stroke: #313139;" rx="1" ry="1" />
      <path :d="d" :style="styleObject" />
      <!-- <text x="26" y="9" font-size="0.7em" fill="#fff">{{percent}}</text> -->
    </svg>
  </div>
</template>
<script>
export default {
  name: 'battery',
  data: function () {
    return {
    }
  },
  props: {
    width: {
      type: String,
      default: '100%'
    },
    value: {
      type: Number,
      required: true,
      default: 0
    },
    upperLimit: {
      type: Number,
      default: 100
    }
  },
  methods: {
    a: function () {}
  },
  computed: {
    percent: function () {
      let percent = (this.value / this.upperLimit) * 100
      // let percent = this.value
      // return `${percent}%`
      // 修复js浮点计算错误bug
      return `${percent.toFixed(0)}`
    },
    d: function () {
      let percent = (this.value / this.upperLimit * 100)
      // 修改电池长度
      let len = percent * (22 / 100)
      // return `M2.5,5 L${len},5`
      return `M${24 - len}, 5 L24, 5`
    },
    styleObject: function () {
      let color
      if (this.value <= 10) {
        color = 'red'
      } else if (this.value > 10 && this.value <= 20) {
        color = 'orange'
      } else if (this.value > 20) {
        color = '#5FD700'
      }
      return {
        'stroke': color,
        'stroke-width': 7,
        'fill': color,
        'opacity': 1
      }
    }
  }
}
</script>
<style scoped lang="less">
.battery {
  position: relative;
  // height: 100%;
  cursor: default;
}
</style>


